import React, {useState} from 'react';
import {
    BodyWrapper,
    SqrImageContainter,
    SqrImage,
    WorkerImage,
    DecoratioLine,
    TitleSection,
    ContentSection,
    Section,
    ClientsSection,
    ClientsImage,
    HeroBtnWrapper,
    NavBtnLink,
    ArrowForward,
    ArrowRight,
    ContactSection,
    ServicesSection,
} from './Body.elemets';

import Politics from './Politics/Politics'
import Contact from './Contact/Contact'

import Image1 from '../../static/images/home-bianco-squares.png'
import Image2 from '../../static/images/bianco-worker.png'

const Body = () => {
    const [hover, setHover] = useState(false)

    const onHover = () => {
        setHover(!hover)
    }

    return (
        <>  

            <div id="us">
                <SqrImageContainter>
                    <SqrImage src={Image1} alt=""/>
                </SqrImageContainter>                
                <BodyWrapper lefted={true}>

                    <Section lefted={true}>
                        <DecoratioLine/>
                        <TitleSection>TECNOLOGIA E INSTRUMENTAL PARA OBRAS DE GRAN APLITUD</TitleSection>
                        <ContentSection>
                            Participamos en la ejecucin de obras de montaje en los distintos sectores 
                            industriales, brindando un ptimo servicio y respaldo operativo a travs de 
                            nuestros recursos humanos altamente calificados.
                        </ContentSection>
                    </Section>

                    <Section>
                        <DecoratioLine/>
                        <TitleSection>TECNOLOGIA E INSTRUMENTAL PARA OBRAS DE GRAN APLITUD</TitleSection>
                        <ContentSection>
                            Participamos en la ejecucin de obras de montaje en los distintos sectores 
                            industriales, brindando un ptimo servicio y respaldo operativo a travs de 
                            nuestros recursos humanos altamente calificados.
                        </ContentSection>
                    </Section>

                    <Section>
                        <WorkerImage src={Image2} />
                    </Section>

                    <Section lefted={true}>
                        <DecoratioLine/>
                        <TitleSection>TECNOLOGIA E INSTRUMENTAL PARA OBRAS DE GRAN APLITUD</TitleSection>
                        <ContentSection>
                            Participamos en la ejecucin de obras de montaje en los distintos sectores 
                            industriales, brindando un ptimo servicio y respaldo operativo a travs de 
                            nuestros recursos humanos altamente calificados.
                        </ContentSection>
                    </Section>

                </BodyWrapper>
                
                <Politics/>
            </div>


            <BodyWrapper lefted={false}>
                <div id="services">
                    <ClientsSection>
                        <TitleSection>NUESTROS CLIENTES</TitleSection>
                        <ClientsImage/>
                    </ClientsSection>

                    <ServicesSection>
                        <TitleSection>LOREM IPSUM DOLOR AMET, CONSECTUER</TitleSection>
                        <ContentSection>
                            Lorem ipsum dolor sit amet, consectetuer adipiscing elit.
                        </ContentSection>
                        <HeroBtnWrapper>
                            <NavBtnLink to="/" onMouseEnter={onHover} onMouseLeave={onHover}>NUESTROS SERVICIOS {hover ? <ArrowForward /> : <ArrowRight />}</NavBtnLink>
                        </HeroBtnWrapper>
                    </ServicesSection> 
                </div>

                <ContactSection id="contact">
                    <TitleSection>CONTACTO</TitleSection>
                    <Contact/>
                </ContactSection>


            </BodyWrapper>
            
        </>
    )
};

export default Body