import styled from 'styled-components';
import { MdArrowForward, MdKeyboardArrowRight } from 'react-icons/md';
import {Link as LinkR} from 'react-router-dom';
/* import HeroImage from '../../static/images/herobianco.jpg'; */

const colors = {
    "dark":"#182740",
    "light":"#f8fbff",
    "primary1":"#3563ab",
    "primary2":"#e1850c",
    "primary3":"#30abcc",
}

export const HeroContainer = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 30px;
    height: 100vh;
    position: relative;
    z-index: 1;

    :before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: linear-gradient(180deg, rgba(0,0,0,0.2) 0%, rgba(0,0,0,0.3) 100%), linear-gradient(180deg, rgba(0,0,0,0.2) 0%, transparent 100%);
        z-index: 2;
    }
`

