export const WhiteLine = styled.div`
    border: solid 2px;
    border-color: ${colors.light};
    max-width: 250px;

    @media screen and (max-width: 768px) {
        max-width: 200px;
    }

    @media screen and (max-width: 480px) {
        max-width: 150px;
    }
`;

export const HeroContent = styled.div`
    z-index: 3;
    left: 10%;
    max-width: 900px;
    position: absolute;
    padding: 8px 24px;
    display: flex;
    flex-direction: column;
    align-items: left;
    
    
`

export const HeroTitle = styled.p`
    margin: 15px 0 15px;
    color: ${colors.light};
    font-size: 3rem; 
    max-width: 780px;
    font-weight: bold;
    text-transform: uppercase;

    @media screen and (max-width: 768px) {
        font-size: 2.5rem; 
    }

    @media screen and (max-width: 480px) {
        font-size: 2rem; 
    }

`

export const HeroMsj = styled.h1`
    color: ${colors.light};
    font-size: 1.5rem;
    text-align:left;
    font-weight: normal;
    padding: 0;
    line-height : 35px;

    @media screen and (max-width: 768px) {
        font-size: 1.5rem; 
    }

    @media screen and (max-width: 480px) {
        font-size: 1.125rem; 
    }
`

export const HeroBtnWrapper = styled.div`
    margin-top: 25px;
    font-size: 1.25rem;
    max-width: 300px;
`
export const NavBtnLink = styled(LinkR)`
    border-radius: 10px;
    background: ${({primary}) => (primary ? '#010606' : '#9D43FF90') };
    white-space: nowrap;
    padding: ${({big}) => (big ? '12px 48px' : '10px 30px')};
    color: #ffffff;
    font-size: ${({fontBig}) => (fontBig ? '1.125rem' : '0.875rem')};
    outline: none;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.2s ease-in-out;
    border: none;
    text-decoration: none;
    cursor: pointer;

    &:hover{
        transition: all 0.2s ease-out;
        background: ${({primary}) => (primary ? '#ffffff' : '#C300EB99') };
        border: none;
        box-shadow: -1px -1px 5px #9D43FF50, 1px 1px 5px #9D43FF50;
    };
`;

export const ArrowForward = styled(MdArrowForward)`
    margin-left: 20px;
    font-size: 1.25rem;
`

export const ArrowRight = styled(MdKeyboardArrowRight)`
    margin-left: 20px;
    font-size: 1.25rem;
`


