import React, {useState} from 'react';
/* import { Button } from '../Button.element'; */
import {
    HeroContainer,
    HeroBg,
    ImageBg,
    HeroContent,
    HeroTitle,
    HeroMsj,
    NavBtnLink,
    HeroBtnWrapper,
    ArrowForward,
    ArrowRight,
    WhiteLine,

} from './Hero.elements';


const Hero = () => {
    const [hover, setHover] = useState(false)

    const onHover = () => {
        setHover(!hover)
    }
 


    return (
        <HeroContainer>
            
{/*             <HeroBg>
                <ImageBg/>
            </HeroBg> */}
            <HeroContent>
                <WhiteLine/>
                <HeroTitle>
                    MS DE 10 AOS EN EL MERCADO
                </HeroTitle>
                <HeroMsj>
                    Especialistas en montajes, construccin de maquinaria y estructuras metlicas
                    para plantas de proceso
                </HeroMsj>
                
            </HeroContent>

        </HeroContainer>

    )
}

export default Hero