import styled from 'styled-components';
import {ReactComponent as email} from '../../../static/images/email.svg';
import {ReactComponent as phone} from '../../../static/images/phone.svg';
import {ReactComponent as location} from '../../../static/images/location.svg';

const colors = {
    "dark":"#182740",
    "light":"#f8fbff",
    "primary1":"#3563ab",
    "primary2":"#e1850c",
    "primary3":"#30abcc",
}

export const ContactWrapper = styled.div`
    display: flex;
    margin-top: 50px;
    vertical-align: top;
    @media screen and (max-width: 768px) {
        display: inline-block;
    }
`;

export const MapSection = styled.div`
    max-width: 400px;
    min-width: 300px;
    width: 100%;
    margin: 0 auto 0;
    text-align: left;
    @media screen and (max-width: 768px) {
        margin-bottom: 40px;
        text-align: center;
    }
`;

export const Form = styled.form`
    //background: #151216;
    height: auto;
    width: 100%;
    z-index: 1;
    display: grid;
    max-width: 400px;
    min-width: 300px;
    margin: 0 auto;
    padding: 45px 35px;
    border-radius: 15px;

    @media screen and (max-width: 400px) {
        padding: 32px 32px;
    }
`;

export const FormInput = styled.input`
    padding: 16px 16px;
    background: ${colors.light};
    margin-top: 0px;
    margin-bottom: 35px;
    border: 1px solid #707070;
    border-radius: 15px;
    color: ${colors.dark};

    &:focus {
        outline: none !important;
        border:1px solid ${colors.primary1};
    }
`;

export const TextInput = styled.textarea`
    padding: 16px 16px;
    background: ${colors.light};
    margin-top: 0px;
    margin-bottom: 35px;
    border: 1px solid #707070;
    border-radius: 15px;
    color: ${colors.dark};
    resize: none;

    &:focus {
        outline: none !important;
        border:1px solid ${colors.primary1};
    }
`;

export const FormBtn = styled.button`
    border-radius: 10px;
    border: 1px solid transparent;
    background: ${colors.primary1};
    padding: 16px 0;
    color: ${colors.light};
    font-size: 0.875rem;
    outline: none;
    transition: all 0.2s ease-in-out;
    text-transform: uppercase;
    cursor: pointer;

    &:hover{
        transition: all 0.2s ease-in-out;
        border-color: transparent;
        background: linear-gradient(135deg, ${colors.primary1} 0%, ${colors.primary3} 100%);
        box-shadow: -1px -1px 4px ${colors.primary1}, 1px 1px 4px ${colors.primary3};
        transform: scale(1.005);
    };
`

export const IconText = styled.div`
    display: flex;
    font-size: 1.2rem;
    margin-bottom: 1.5rem;
`;

export const Text = styled.p`
    color: ${colors.dark};
    font-weight: bold;
    margin-left: 10px;
`;

export const Email = styled(email)`
    margin-top: 2px;
    width: 1.3rem;
    height: 1.3rem;
`;

export const Phone = styled(phone)`
    margin-top: 2px;
    width: 1.3rem;
    height: 1.3rem;
`;

export const Location = styled(location)`
    margin-top: 2px;
    width: 1.3rem;
    height: 1.3rem;
`;




