import styled from 'styled-components';
import {ReactComponent as Clients} from '../../static/images/clients.svg';
import { MdArrowForward, MdKeyboardArrowRight } from 'react-icons/md';
import {Link as LinkR} from 'react-router-dom';


const colors = {
    "dark":"#182740",
    "light":"#f8fbff",
    "primary1":"#3563ab",
    "primary2":"#e1850c",
    "primary3":"#30abcc",
}

export const BodyWrapper = styled.div`
    background-color: ${colors.light};
    padding: 30px 10% 0;
    padding-top: 100px;
    height: auto;
    overflow:auto;

    clip-path: ${({lefted}) => (lefted ? 
        'polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%, 0% 80%, 25% 55%, 0% 30%)' : 'polygon(0% 0%, 100% 0%, 100% 20%, 75% 45%, 100% 70%, 100% 100%, 0% 100%)'
    )};

    @media screen and (max-width: 768px) {
        padding-top: 50px;
        clip-path: none;
    }
`;

export const SqrImageContainter = styled.div`
    position: absolute;
    z-index: 1;
    top: 75%;
    right: 5%; 

    @media screen and (max-width: 900px) {
        top: 82%;
        right: 0%; 
    }

    @media screen and (max-width: 768px) {
        display: none;
    }

`
export const SqrImage = styled.img`
    max-width: 500px;

    @media screen and (max-width: 900px) {
        width: 350px;
    }

`

export const WorkerImage = styled.img`
    max-width: 800px;
    width: 100%;
`

export const Section = styled.div`
    max-width: 70%;
    margin: 90px 0 90px;
    font-size: 1.3rem;
    float: ${({lefted}) => (lefted ? 'left' : 'right')};

    @media screen and (max-width: 768px) {
        max-width: 100%;
        margin: 60px 0 60px;
        font-size: 1.1rem;
    }
`

export const DecoratioLine = styled.div`
    border: solid 1px;
    border-color: ${colors.dark};
    max-width: 150px;

    @media screen and (max-width: 768px) {
        max-width: 120px;
    }

    @media screen and (max-width: 480px) {
        max-width: 100px;
    }
`

export const TitleSection = styled.h3`
    color: ${colors.dark};
    margin: 20px 0 20px;
`

export const ContentSection = styled.p`
    color: ${colors.dark};
    line-height: 25px;
`

export const ClientsSection = styled.div`
    font-size: 1.3rem;
    text-align: center;
`;

export const ClientsImage = styled(Clients)`
    width: 100%;

    @media screen and (max-width: 768px) {
        max-height: 150px;
    }
`;

export const ServicesSection = styled.div`
    max-width: 70%;
    margin: 90px 0 90px;
    font-size: 1.3rem;

    @media screen and (max-width: 768px) {
        max-width: 100%;
    }
`


export const HeroBtnWrapper = styled.div`
    margin-top: 25px;
    font-size: 1.25rem;
    max-width: 300px;
`
export const NavBtnLink = styled(LinkR)`
    border-radius: 10px;
    background: ${({primary}) => (primary ? '#010606' : colors.primary1 + "80") };
    white-space: nowrap;
    padding: ${({big}) => (big ? '12px 48px' : '10px 30px')};
    color: ${colors.light};
    font-size: ${({fontBig}) => (fontBig ? '1.125rem' : '0.875rem')};
    outline: none;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.2s ease-in-out;
    border: none;
    text-decoration: none;
    cursor: pointer;

    &:hover{
        transition: all 0.2s ease-out;
        background: ${({primary}) => (primary ? colors.light : colors.primary1) };
        border: none;
        box-shadow: -1px -1px 5px ${colors.primary1}, 1px 1px 5px ${colors.primary3};
    };
`;

export const ArrowForward = styled(MdArrowForward)`
    margin-left: 20px;
    font-size: 1.25rem;
`

export const ArrowRight = styled(MdKeyboardArrowRight)`
    margin-left: 20px;
    font-size: 1.25rem;
`

export const ContactSection = styled.div`
    width: 100%;
    height: auto;
    font-size: 1.3rem;
    text-align: center;
    margin: 110px 0 90px;
`;

