import React from 'react';
import {
    Wrapper,
    Header,
    Title,
    TextBody,
    Ol,
    Li,
    PoliticsDecoration,
    BiancoLogo,
    LogoContainer,
} from './Politics.elemets';


class Politics extends React.Component{
    constructor(props){
        super(props)
    };

    render(){
        return(
            <>
                <Wrapper>
                    <Header>
                        <Title active>POLTICAS</Title>
                        <Title>MISIN Y VISIN</Title>
                        <Title>RECURSOS HUMANOS</Title>
                    </Header>

                    <TextBody>
                        <p>
                            Somos un equipo de trabajo cuyas acciones diarias las ejecutamos con una elevada vocacin de servicio a
                            los Clientes en nuestra visin de empresa de categora mundial, basadas en los siguientes principios:
                        </p>
                        <Ol>
                            <Li>INTEGRIDAD PERSONAL como expresin de disciplina, orden, respeto, honestidad y entusiasmo.</Li>
                            <Li>CREATIVIDAD E INNOVACIN como parte de nuestro reto diario para el mejoramiento contino.</Li>
                            <Li>PRODUCTIVIDAD en nuestro trabajo y en el empleo de los recursos materiales.</Li>
                            <Li>
                                CONSCIENCIA en la prctica de un trabajo libre de errores y en el COMPROMISO leal con la 
                                institucin y con las realizaciones de calidad.
                            </Li>
                            
                        </Ol>
                        
                        <PoliticsDecoration/>
                        
                    </TextBody>
                    <LogoContainer>
                        <BiancoLogo/>
                    </LogoContainer>
                    

                </Wrapper>

            </>
        )
    }
}

export default Politics;