import styled from 'styled-components';
import {ReactComponent as Decoration} from '../../../static/images/politics-decoration.svg';
import {ReactComponent as Logo} from '../../../static/images/bianco-logo.svg';

const colors = {
    "dark":"#182740",
    "light":"#f8fbff",
    "primary1":"#3563ab",
    "primary2":"#e1850c",
    "primary3":"#30abcc",
}

export const Wrapper = styled.div`
    position: relative;
    height: 100%;
    width: 100%;
    background-color: #e9ecef;
    overflow: auto;
    line-height: 2.5rem;
`;

export const Header = styled.div`
    display: flex;
    margin-top: 25px;
    font-size: 1.5rem;

    @media screen and (max-width: 768px) {
        font-size: 1rem;
    }

    @media screen and (max-width: 400px) {
        font-size: 0.8rem;
    }
`;

export const Title = styled.h4`
    color: ${colors.dark};
    margin: 0 auto;
    text-transform: uppercase;
`;

export const TextBody = styled.div`
    color: ${colors.dark};
    margin: 2.5rem 10% 0;
    font-size: 1.2rem;
    @media screen and (max-width: 768px) {
        font-size: 0.9rem;
    }
`;

export const Ol = styled.ol`
    list-style-position:inside;
`;

export const Li = styled.li`
    margin-top: 2rem;
    @media screen and (max-width: 768px) {
        margin-top: 1rem;
    }
`;

export const PoliticsDecoration = styled(Decoration)`
    position: absolute;
    bottom: 2%;
    right: 2%;

    @media screen and (max-width: 768px) {
        max-width: 150px;
    }
`;

export const BiancoLogo = styled(Logo)`
`;

export const LogoContainer = styled.div`
    text-align: center;
    margin: 1rem 0 1rem;
`;

