import styled from 'styled-components';
import { ReactComponent as facebook } from '../../static/images/facebook.svg';
import { ReactComponent as linkedin } from '../../static/images/linkedin.svg';

const colors = {
    "dark":"#182740",
    "light":"#f8fbff",
    "primary1":"#3563ab",
    "primary2":"#e1850c",
    "primary3":"#30abcc",
}

export const FooterWrapper = styled.div`
    background-color: ${colors.light};
    z-index: 1;
    width: 100%;
    justify-content: center;
    align-items: center;
    height: 10vh;
    display: flex;
    justify-content: space-between;
    font-size: 1rem;

    @media screen and (max-width: 768px) {
        font-size: 0.9rem;
    }
`;

export const Container = styled.div`
    margin: 0 10% 0;
    font-weight: lighter;
    font-size: 0.9rem;
    color: ${colors.dark};
    @media screen and (max-width: 768px) {
        margin: 0 5% 0;
    }
    @media screen and (max-width: 430px) {
        font-size: 0.8rem;
    }
`;

export const IconsContainer = styled.div`
    display: flex;
    justify-content: space-between;
`;

export const Facebook = styled(facebook)`
    width: 1.8rem;
    height: 1.8rem;
    margin-right: 0.5rem;
    @media screen and (max-width: 430px) {
        width: 1.6rem;
        height: 1.6rem;
    }
`;

export const Linkedin = styled(linkedin)`
    width: 1.8rem;
    height: 1.8rem;
    @media screen and (max-width: 430px) {
        width: 1.6rem;
        height: 1.6rem;
    }
`;